import { Component, Prop, Event, EventEmitter, State, h, Host } from '@stencil/core';
import { BhProduct } from '../bh-product-switcher/bh-product-switcher';

export type LogoType = 'actabl' | 'al' | 'he' | 'ps' | 'tr';

@Component({
  tag: 'bh-logo-box',
  styleUrl: 'bh-logo-box.css',
  shadow: true,
})
export class BhLogoBox {
  /**
   * Type of logo to display
   */
  @Prop() logoType: LogoType = 'actabl';

  /**
   * Custom logo source URL. If provided, overrides the default logo path.
   */
  @Prop() logoSrc?: string;

  /**
   * Base path for asset URLs. Defaults to '/bellhop/assets'
   */
  @Prop() assetBasePath: string = '/bellhop/assets';

  /**
   * Array of products to display in the switcher
   */
  @Prop() products: BhProduct[] = [
    {
      id: 'alice',
      name: 'Alice',
      description: 'Housekeeping & Guest Services',
      logo: './assets/logos/AL.svg',
    },
    {
      id: 'hotel-effectiveness',
      name: 'Hotel Effectiveness',
      description: 'Labor planning',
      logo: './assets/logos/HE.svg',
    },
    {
      id: 'profit-sword',
      name: 'ProfitSword',
      description: 'B.I and Finance',
      logo: './assets/logos/PS.svg',
    },
    {
      id: 'transcendent',
      name: 'Transcendent',
      description: 'Asset Management & CapEx',
      logo: './assets/logos/TR.svg',
    },
  ];

  /**
   * Event emitted when a product is selected
   */
  @Event() bhProductSelect!: EventEmitter<BhProduct>;

  @State() isOpen: boolean = false;
  @State() anchorPosition = { top: 0, left: 0 };

  private logoActiveRef?: HTMLDivElement;

  private handleLogoClick = () => {
    if (this.logoActiveRef) {
      const rect = this.logoActiveRef.getBoundingClientRect();
      this.anchorPosition = {
        top: rect.bottom + 8,
        left: rect.left,
      };
      this.isOpen = !this.isOpen;
    }
  };

  private handleClose = () => {
    this.isOpen = false;
  };

  private handleProductSelect = (event: CustomEvent<BhProduct>) => {
    this.bhProductSelect.emit(event.detail);
    this.isOpen = false;
  };

  private getLogoSrc = () => {
    if (this.logoSrc) {
      return this.logoSrc;
    }

    const logoMap: Record<LogoType, string> = {
      actabl: 'actabl.svg',
      al: 'AL.svg',
      he: 'HE.svg',
      ps: 'PS.svg',
      tr: 'TR.svg',
    };

    const logoFile = logoMap[this.logoType] || 'actabl.svg';
    return `${this.assetBasePath}/logos/${logoFile}`;
  };

  render() {
    const logoBoxClasses = {
      'logo-box': true,
      'dropdown-open': this.isOpen,
    };

    const logoClasses = {
      'logo': true,
      [`logo-${this.logoType}`]: true,
    };

    return (
      <Host>
        <div class={logoBoxClasses}>
          <div
            ref={(el) => (this.logoActiveRef = el)}
            class="logo-active"
            onClick={this.handleLogoClick}
          >
            <img
              src={this.getLogoSrc()}
              alt={`${this.logoType} logo`}
              class={logoClasses}
            />
          </div>
          <div class="logo-more-products"></div>
        </div>

        <bh-product-switcher
          isOpen={this.isOpen}
          anchorPosition={this.anchorPosition}
          products={this.products}
          onBhClose={this.handleClose}
          onBhProductSelect={this.handleProductSelect}
        ></bh-product-switcher>
      </Host>
    );
  }
}
